import { useState } from 'react';
import PropTypes from 'prop-types';
import Product from 'views/product';

// material-ui
import { Button, Dialog, DialogTitle } from '@mui/material';

function ProductSelector({ langPack, dialogOpen, setDialogOpen, onProductConfirm }) {
    //cloud 相关
    let [cloudArr, setCloudArr] = useState([]); //导入的云图片

    //Cloud组件 相关事件
    let onProductSelected = (value) => {
        if (!value) return;
        setCloudArr(value);
    }; //选择了产品，则存到当前component里

    let onDialogClose = (submit) => {
        setDialogOpen(false);
        if (submit) return onProductConfirm(cloudArr);
        return setCloudArr([]); //清除传入值
    }; //对话框关闭

    return (
        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
            <Dialog onClose={() => onDialogClose(true)} open={dialogOpen}>
                <DialogTitle>{langPack.steps.selectFromCloud}</DialogTitle>
                <Product onProductSelected={onProductSelected} />
                <Button variant="contained" color="primary" onClick={() => onDialogClose(true)}>
                    {langPack.common.yes}
                </Button>
                <Button variant="contained" color="error" onClick={() => onDialogClose()}>
                    {langPack.common.no}
                </Button>
            </Dialog>
        </div>
    );
}

ProductSelector.propTypes = {
    dialogOpen: PropTypes.bool,
    setDialogOpen: PropTypes.func,
    onProductConfirm: PropTypes.func,
    langPack: PropTypes.object
};

export default ProductSelector;
